* {
  box-sizing: border-box;
}

body{
  box-sizing: border-box;
  overflow-x: hidden;
  background-color: #F5F5F5;
 
}


.main--divs{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.main--divs  section {
  width: 100%;

}


.main--header{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: Rubik,sans-serif;
  font-size: 50px;
}

.about--con{
  height: 100vh;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: flex-start;

  


}

.bio--con{
  padding: 200px;
  font-size: 20px;
  font-family: Rubik,sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  text-align: center;
  line-height: 1.6rem;
}

.aboutme--con{
  padding: 50px;
  font-family: Rubik,sans-serif;
}

.about--con div{
 

}

.first--image{
  width: 200px;
  z-index: 999;
}

.project--con{
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: space-evenly;
  overflow: hidden;
  height: 100vh;
}


.site--title{
  font-family: 'Montserrat', sans-serif;
  padding: 20px;
  font-size: 40px;
  color: rgb(51, 51, 51);
  font-weight: 600;
}

.project--div{
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  color: rgb(51, 51, 51);
  font-weight: 800;
  display: flex;
  flex-direction: column;
  align-items: center;
 
}

.languages--used{
  
  align-items: center;
  font-weight: 200;
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  color: rgb(51, 51, 51);
}

.feature--list{
  
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  color: rgb(51, 51, 51);
  font-weight: 200;
  
  
  
}

.link--parent{
 
    text-decoration: none;
    color: rgb(51, 51, 51);
  padding-bottom: 10px;
    border-bottom: 1px solid rgb(51, 51, 51);
    display: flex;
  }


.second--image{
  width: 300px
}

.languages--div{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  
}

.language--con{
  display: flex;
  flex-direction: row;
  padding: 20px;
  
}

.language--con div {
  padding: 30px;
}

.front--con{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  border: 1px solid black;
  padding: 20px;
  border-radius: 16px;
  background-color: black;
  margin: 20px;

}

.back--con{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  border: 1px solid black;
  padding: 20px;
  border-radius: 16px;
  background-color: black;
  margin: 20px;
}

.hello--div{
  font-family: 'Montserrat', sans-serif;
  font-size: 40px;
}

.list--skills li{
  list-style: none;
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  padding: 10px;
  font-weight: bold;
  color: white;
  background-color: black;
  
 
 
 }

  .contact--div{
    height: 100vh;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    justify-content: space-around;
 
  }

.contact--title div{
  padding: 20px;
}



.info--title {
  font-family: 'Montserrat', sans-serif;
  font-weight: 900;
  color: rgb(51, 51, 51);
  font-size: 57px;
}

.info--title.number{
  font-size: 20px
}

.info--title.email{
  font-size: 20px
}
 .front--title, .back--title{
  font-family: 'Montserrat', sans-serif;
 }




form{
  display: flex;
  
  flex-direction: column;
}


form input, textarea, button{
  margin: 20px;
  padding:  25px 60px;
  background-color: white;
  border: 2px solid black;
  border-radius: 10px;
}

@keyframes moveBackAndForth {
  0% {
    transform: translateX(0);
  }
  
  0% {
    transform: translateX(20%);
  }


}

@keyframes swipe-in-text {
  0% {
    
    left:120;
    width: 230px;
  }
  
  50% {
    left: 120;
    width: 230px;;
    
  }

  100%{
    left: 120;
    width: 0%
  }
}

@keyframes bar {
  0%
  {
    width: 100%;
    left: 0
  }

  50%
  {
    width: 100%;
    left: 0
  }
  100%
  {
    width: 0%;
    left: 0
  }
}

@media (max-width: 896px){
  body{
    overflow-x: hidden !important;

  }
  .main--header{
    font-size: 12px;
    
  }
  .main--header h1{
    font-size: 20px;
    padding: 20px
  }

  .aboutme--con{
    padding: 20px;
    font-family: Rubik,sans-serif;
   
  }

  .about--con{
    height: 70vh
  }


.bio--con{
  padding: 100px;
  font-size: 15px;
  font-family: Rubik,sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  text-align: center;
  line-height: 1.6rem;
  
}

.project--con {
 flex-direction: column;
 
 height: 250vh;

}

img{
  z-index: 9999;
}

.language--con{
  flex-direction: column;
 
}

.languages--div{
  height: 150vh


}

.hello--div{
  font-size: 20px;
  font-weight: bolder;
  margin-bottom: 20px;
}

  .contact--div{
    flex-direction: column;
    
  }


  .info--title{
    font-size: 30px;
  }

}